<template>
  <view class="container">
    <reward-card
        v-if="showRewardCard"
        :title="rewardTitle"
        :message="rewardMessage"
        :stats="rewardStats"
        :habit-info="currentHabit"
        :mode="viewMode"
        :show="showRewardCard"
        @saveNote="handleSaveNote"
        @updateShow="showRewardCard = $event"
    />
    <view class="header">
      <view class="date-info">
        <view class="date-content">
          <text class="year">{{ currentYear }}Âπ¥Á¨¨{{ yearWeek }}Âë®</text>
          <text class="month-week">{{ currentMonth }}ÊúàÁ¨¨{{ monthWeek }}Âë®</text>
        </view>
        <view class="flip-clock">
          <text class="time-unit">{{ formatNumber(hours) }}</text>
          <text class="time-separator">:</text>
          <text class="time-unit">{{ formatNumber(minutes) }}</text>
          <text class="time-separator">:</text>
          <text class="time-unit">{{ formatNumber(seconds) }}</text>
        </view>
      </view>
      <view class="header-row">
        <view class="habit-stats">
          <view class="stat-item">
            <text class="stat-value">{{ habits.length }}</text>
            <text class="stat-label">ÊÄª‰π†ÊÉØ</text>
          </view>
          <view class="stat-item">
            <text class="stat-value">{{ getTodayCompletedCount }}</text>
            <text class="stat-label">‰ªäÊó•Â∑≤ÊâìÂç°</text>
          </view>
        </view>
        <view v-if="isOrderMode" class="complete-order-btn" @tap="completeOrder">ÂÆåÊàêÊéíÂ∫è</view>
        <view v-else class="view-switch">
          <view
              class="switch-btn"
              :class="{ active: viewMode === 'week' }"
              @tap="switchView('week')"
          >
            <text class="icon">üìÖ</text>
            <text>Âë®ËßÜÂõæ</text>
          </view>
          <view
              class="switch-btn"
              :class="{ active: viewMode === 'month' }"
              @tap="switchView('month')"
          >
            <text class="icon">üìä</text>
            <text>ÊúàËßÜÂõæ</text>
          </view>
        </view>
        <view class="backup-actions">
          <button class="backup-btn" @tap="showBackupOptions">
            <text class="icon">üì§</text>
          </button>
        </view>
      </view>
    </view>

    <view class="empty-state" v-if="habits.length === 0">
      <text class="empty-icon">üìù</text>
      <text class="empty-title">ÂºÄÂßãÂüπÂÖªÊñ∞‰π†ÊÉØ</text>
      <text class="empty-desc">ÁÇπÂáªÂè≥‰∏ãËßíÁöÑÂä†Âè∑ÊåâÈíÆÊ∑ªÂä†‰Ω†ÊÉ≥Ë¶ÅÂüπÂÖªÁöÑ‰π†ÊÉØ</text>
    </view>

    <view class="view-container" @touchstart="handleTouchStart" @touchend="handleTouchEnd">
      <view
          class="view-page habits-list"
          :hidden="viewMode !== 'week'"
          :style="{ transform: `translateX(${translateX}px)` }"
      >
        <view
            v-for="(habit, index) in habits"
            :key="habit.id"
            class="habit-card"
            :class="{
            'is-flipped': flippedCards[habit.id],
            'is-order-mode': isOrderMode,
            'no-interaction': isOrderMode,
            [habit.animating]: habit.animating
          }"
            @longpress="!isOrderMode && handleCardLongPress(habit)"
            @touchend="!isOrderMode && handleCardTouchEnd(habit)"
            @tap="!isOrderMode && goToStats(habit)"
        >
          <!-- Âç°ÁâáÊ≠£Èù¢ -->
          <view class="card-face card-front">
            <view class="habit-header">
              <view class="title-section">
                <view class="habit-icon" :style="{ 'background-color': habit.color }">
                  <text class="emoji-icon">{{ habit.icon }}</text>
                </view>
                <text class="habit-title">{{ habit.title }}</text>
              </view>
              <view class="more-actions" @tap.stop="showActionSheet(habit)">
                <text class="more-icon">‚ãÆ</text>
              </view>
            </view>

            <!-- ÊéíÂ∫èÊåâÈíÆ -->
            <view class="order-buttons" v-if="isOrderMode">
              <view
                  class="order-btn up"
                  :class="{ disabled: index === 0 }"
                  @tap.stop="moveHabitUp(index)"
              >
                <text class="order-icon">‚Üë</text>
              </view>
              <view
                  class="order-btn down"
                  :class="{ disabled: index === habits.length - 1 }"
                  @tap.stop="moveHabitDown(index)"
              >
                <text class="order-icon">‚Üì</text>
              </view>
            </view>

            <!-- ‰π†ÊÉØÂÜÖÂÆπ -->
            <view class="habit-content">
              <view class="progress-view">
                <view
                    v-for="(day, i) in 7"
                    :key="i"
                    class="week-day"
                >
                  <text class="day-label">{{ weekDays[i] }}</text>
                  <view
                      class="day-box"
                      :class="{
                      completed: isCompletedForDay(habit, i),
                      today: isWeekToday(i),
                      future: isFutureDay(i)
                    }"
                      @tap.stop="handleDayClick(habit, i)"
                  >{{ getDayDate(i) }}
                  </view>
                </view>
              </view>
              <view class="expand-button" @tap.stop="toggleCardExpand(habit.id)">
                <text class="expand-text">{{ expandedCards[habit.id] ? 'Êî∂Ëµ∑ÂøÉÂæó' : 'Êü•ÁúãÂøÉÂæó' }}</text>
                <text class="expand-icon">{{ expandedCards[habit.id] ? '‚Üë' : '‚Üì' }}</text>
              </view>
            </view>

            <!-- Á¨îËÆ∞ÂÆπÂô® -->
            <view class="notes-container" v-show="expandedCards[habit.id]">
              <view class="empty-notes" v-if="getWeekNotes(habit).length === 0">
                <text>Êú¨Âë®ËøòÊ≤°ÊúâËÆ∞ÂΩïÂøÉÂæóÂì¶~</text>
              </view>
              <view class="notes-list" v-else>
                <view
                    v-for="(note, index2) in getWeekNotes(habit)"
                    :key="index2"
                    class="note-item"
                >
                  <view class="note-time">
                    <view class="time-left">
                      <text class="date">{{ formatNoteTime(note.timestamp).date }}</text>
                      <text class="time">{{ formatNoteTime(note.timestamp).time }}</text>
                    </view>
                    <view class="time-right">{{ formatNoteTime(note.timestamp).relative }}</view>
                  </view>
                  <view class="note-content">{{ note.content }}</view>
                </view>
              </view>
            </view>
          </view>

          <!-- Âç°ÁâáËÉåÈù¢ -->
          <view class="card-face card-back">
            <view class="flag-banner" v-if="habit.flag">
              <text class="flag-icon">üéØ</text>
              <text class="flag-text">{{ habit.flag }}</text>
            </view>
            <view class="flag-banner" v-else>
              <text class="flag-icon">‚ú®</text>
              <text class="flag-text">ÁÇπÂáª‰π†ÊÉØÂç°ÁâáÂè≥‰∏äËßíÁöÑÊåâÈíÆ\nÈÄâÊã©ÁºñËæëÊ∑ªÂä†ÁõÆÊ†áÔºÅ</text>
            </view>
            <view class="create-time">
              <text class="time-label">ÂàõÂª∫‰∫é</text>
              <text class="time-value">{{ formatCreateTime(habit.createTime) }}</text>
            </view>
          </view>
        </view>
      </view>

      <!-- ÊúàËßÜÂõæ -->
      <view
          class="view-page month-habits-grid"
          :hidden="viewMode !== 'month'"
          :style="{ transform: `translateX(${translateX}px)` }"
      >
        <view
            v-for="(habit, index) in habits"
            :key="habit.id"
            class="month-habit-card"
            :class="{ 'no-interaction': isOrderMode }"
            @tap="!isOrderMode && goToStats(habit)"
        >
          <view class="habit-info">
            <view class="habit-icon" :style="{ 'background-color': habit.color }">
              <text class="emoji-icon">{{ habit.icon }}</text>
            </view>
            <text class="habit-title">{{ habit.title }}</text>
          </view>
          <view class="heatmap">
            <view
                v-for="(day, dayIndex) in getDaysInMonth()"
                :key="dayIndex"
                class="heatmap-cell"
                :class="{
                completed: isCompletedForMonthDay(habit, dayIndex + 1),
                today: isMonthToday(dayIndex + 1),
                future: isFutureMonthDay(dayIndex + 1)
              }"
                @tap.stop="handleMonthDayClick(habit, dayIndex + 1)"
            >{{ dayIndex + 1 }}
            </view>
          </view>
          <view class="month-stats">
            <text class="stat">ÂÆåÊàê {{ getMonthCompletionCount(habit) }} Â§©</text>
            <text class="completion-rate">{{ getMonthCompletionRate(habit) }}%</text>
          </view>
        </view>
      </view>
    </view>

    <view class="add-button" v-if="!isOrderMode" @tap="addHabit">
      <text class="plus">+</text>
    </view>
  </view>
</template>

<script>
import {defineComponent} from 'vue'
import RewardCard from '@/components/reward-card.vue'

export default defineComponent({
  components: {
    RewardCard
  },
  data() {
    return {
      weekDays: ['Âë®‰∏Ä', 'Âë®‰∫å', 'Âë®‰∏â', 'Âë®Âõõ', 'Âë®‰∫î', 'Âë®ÂÖ≠', 'Âë®Êó•'],
      habits: [],
      monthHabits: [],
      currentYear: 0,
      currentMonth: 0,
      yearWeek: 0,
      monthWeek: 0,
      showRewardCard: false,
      rewardTitle: '',
      rewardMessage: '',
      rewardStats: null,
      currentHabit: null,
      isNoInteraction: false,
      viewMode: 'week',
      touchStartX: 0,
      touchStartTime: 0,
      translateX: 0,
      monthTranslateX: 0,
      isOrderMode: false,
      flippedCards: {},
      expandedCards: {},
      hours: 0,
      minutes: 0,
      seconds: 0,
      timer: null
    }
  },

  computed: {
    getTodayCompletedCount() {
      const today = new Date()
      const todayTimestamp = new Date(
          today.getFullYear(),
          today.getMonth(),
          today.getDate()
      ).getTime()
      return this.habits.filter(habit =>
          habit.completed.includes(todayTimestamp)
      ).length
    }
  },


  methods: {
    addHabit() {
      uni.navigateTo({
        url: '/pages/add-habit/add-habit'
      })
    },

    loadHabits() {
      const habits = uni.getStorageSync('habits') || []
      this.habits = habits.map(habit => ({
        ...habit,
        icon: habit.icon || this.getCategoryIcon(habit.category),
        color: habit.color || '$theme-color'
      }))
    },

    // Âë®ËßÜÂõæÁõ∏ÂÖ≥ÊñπÊ≥ï
    isCompletedForDay(habit, dayIndex) {
      const timestamp = this.getDayTimestamp(dayIndex)
      return habit.completed.includes(timestamp)
    },

    isWeekToday(dayIndex) {
      const today = new Date()
      const targetDate = this.getDayDate(dayIndex)
      return today.getDate() === targetDate
    },

    isFutureDay(dayIndex) {
      const today = new Date()
      const targetDate = this.getDateFromDayIndex(dayIndex)
      const todayDate = new Date(today.getFullYear(), today.getMonth(), today.getDate())
      const targetDateOnly = new Date(targetDate.getFullYear(), targetDate.getMonth(), targetDate.getDate())
      return targetDateOnly > todayDate
    },

    getDayDate(dayIndex) {
      const date = this.getDateFromDayIndex(dayIndex)
      return date.getDate()
    },

    getDateFromDayIndex(dayIndex) {
      const today = new Date()
      const monday = new Date()
      monday.setDate(today.getDate() - (today.getDay() === 0 ? 6 : today.getDay() - 1))
      const targetDate = new Date(monday)
      targetDate.setDate(monday.getDate() + dayIndex)
      return targetDate
    },

    getDayTimestamp(dayIndex) {
      const date = this.getDateFromDayIndex(dayIndex)
      return new Date(date.getFullYear(), date.getMonth(), date.getDate()).getTime()
    },

    toggleHabitComplete(habit, dayIndex) {
      const timestamp = this.getDayTimestamp(dayIndex)
      const today = new Date()
      const targetDate = this.getDateFromDayIndex(dayIndex)

      if (targetDate > today) {
        uni.showToast({
          title: 'Êú™Êù•ËøòÊú™Êù•Âì¶~',
          icon: 'none',
          duration: 1000
        })
        return
      }

      if (
          targetDate.getDate() !== today.getDate() ||
          targetDate.getMonth() !== today.getMonth() ||
          targetDate.getFullYear() !== today.getFullYear()
      ) {
        uni.showToast({
          title: 'ËØ∑ÁÇπÂáª‰π†ÊÉØÂç°ÔºåÂú®Êó•ÂéÜ‰∏≠ËøõË°åË°•ÊâìÂç°',
          icon: 'none',
          duration: 1000
        })
        return
      }

      const habits = uni.getStorageSync('habits') || []
      const habitIndex = habits.findIndex(h => h.id === habit.id)
      if (habitIndex === -1) return

      const completedIndex = habits[habitIndex].completed.indexOf(timestamp)
      if (completedIndex === -1) {
        habits[habitIndex].completed.push(timestamp)
        const weekStart = this.getDateFromDayIndex(0)
        const weekEnd = this.getDateFromDayIndex(6)
        weekStart.setHours(0, 0, 0, 0)
        weekEnd.setHours(23, 59, 59, 999)

        const weekCompletedCount = habits[habitIndex].completed.filter(time => {
          const date = new Date(time)
          return date >= weekStart && date <= weekEnd
        }).length

        this.showRewardCard = true
        this.rewardTitle = 'ÊâìÂç°ÊàêÂäüÔºÅ'
        this.rewardMessage = ''
        this.currentHabit = {
          id: habit.id,
          name: habit.title,
          icon: habit.icon,
          color: habit.color
        }
        this.rewardStats = {
          count: weekCompletedCount,
          label: 'Êú¨Âë®Â∑≤ÂÆåÊàê'
        }
      } else {
        habits[habitIndex].completed.splice(completedIndex, 1)
      }

      uni.setStorageSync('habits', habits)
      this.loadHabits()
      uni.vibrateShort()
    },

    // Ê£ÄÊü•Êú¨Âë®ÊòØÂê¶ÂÖ®ÈÉ®ÂÆåÊàê
    checkWeekCompletion(habit) {
      return Array(7).fill().every((_, index) => this.isCompletedForDay(habit, index))
    },

    // ÈÄöÁî®ÊñπÊ≥ï
    updateHabitCompletion(habit, timestamp) {
      const habits = uni.getStorageSync('habits') || []
      const habitIndex = habits.findIndex(h => h.id === habit.id)
      if (habitIndex === -1) return

      const completedIndex = habits[habitIndex].completed.indexOf(timestamp)
      if (completedIndex === -1) {
        habits[habitIndex].completed.push(timestamp)
      } else {
        habits[habitIndex].completed.splice(completedIndex, 1)
      }

      uni.setStorageSync('habits', habits)
      this.loadHabits()
      uni.vibrateShort()
    },

    goToStats(habit) {
      uni.navigateTo({
        url: `/pages/habit-stats/habit-stats?id=${habit.id}&title=${encodeURIComponent(habit.title)}`
      })
    },

    getCategoryIcon(category) {
      const icons = {
        workout: 'üèÉ',
        diet: 'ü•ó',
        water: 'üíß',
        sleep: 'üò¥',
        meditation: 'üßò',
        running: '‚öΩ',
        reading: 'üìö',
        coding: 'üíª',
        course: 'üìù',
        cleaning: 'üßπ',
        cooking: 'üç≥',
        finance: 'üí∞',
        plants: 'üå±',
        music: 'üé∏',
        art: 'üé®',
        craft: 'üé≠',
        photo: 'üì∏',
        singing: 'üé§',
        game: 'üéÆ',
        time: '‚è∞',
        planning: 'üìä',
        mood: 'üåà',
        social: 'üë•',
        notes: 'üìù',
        writing: '‚úçÔ∏è',
        custom: '‚ú®',
        other: 'üéØ'
      }
      return icons[category] || 'üéØ'
    },

    getCategoryColor(category) {
      const colors = {
        // ÂÅ•Â∫∑ÁªÑ
        workout: '#FF6B6B',
        diet: '#95E1D3',
        water: '#87CEEB',
        sleep: '#6C5B7B',
        meditation: '#9B89B3',
        running: '#FF6B6B',
        // Â≠¶‰π†ÁªÑ
        reading: '#FFD93D',
        coding: '#6C5B7B',
        course: '#96CEB4',
        // ÁîüÊ¥ªÁªÑ
        cleaning: '#A8E6CF',
        cooking: '#F8B195',
        finance: '#2A363B',
        plants: '#4ECDC4',
        // ÂÖ¥Ë∂£ÁªÑ
        music: '#FF8C94',
        art: '#F67280',
        craft: '#C06C84',
        photo: '#355C7D',
        singing: '#FF8C94',
        game: '#3F72AF',
        // ÊàêÈïøÁªÑ
        time: '#45B7D1',
        planning: '#96CEB4',
        mood: '#9B89B3',
        social: '#F8B195'
      }
      return colors[category] || '$theme-color'
    },

    showActionSheet(habit) {
      const actions = [
        {
          name: 'ÁºñËæë‰π†ÊÉØ',
          action: () => this.editHabit(habit)
        },
        {
          name: 'Ë∞ÉÊï¥È°∫Â∫è',
          action: () => this.toggleOrderMode()
        },
        {
          name: 'Âà†Èô§‰π†ÊÉØ',
          action: () => this.deleteHabit(habit)
        }
      ]

      const systemInfo = uni.getSystemInfoSync()
      uni.showActionSheet({
        itemList: actions.map(item => item.name),
        itemColor: systemInfo.platform === 'ios' ? '#007AFF' : '#2d8cf0',
        success: (res) => {
          if (systemInfo.platform === 'ios') {
            setTimeout(() => {
              actions[res.tapIndex]?.action()
            }, 100)
          } else {
            actions[res.tapIndex]?.action()
          }
        },
        fail: (err) => {
          console.log('ActionSheet ÂÖ≥Èó≠', err)
        }
      })
    },

    editHabit(habit) {
      uni.navigateTo({
        url: `/pages/add-habit/add-habit?id=${habit.id}`
      })
    },

    deleteHabit(habit) {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™‰π†ÊÉØÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ',
        confirmColor: '#FF3B30',
        success: (res) => {
          if (res.confirm) {
            const habits = uni.getStorageSync('habits') || []
            const updatedHabits = habits.filter(h => h.id !== habit.id)
            uni.setStorageSync('habits', updatedHabits)
            this.loadHabits()
            uni.showToast({
              title: 'Âà†Èô§ÊàêÂäü',
              icon: 'success'
            })
          }
        }
      })
    }
  },

  onShow() {
    this.loadHabits()
    this.updateDateInfo()
    this.startClock()
    const hasShownReminder = uni.getStorageSync('has_shown_data_loss_reminder')
    if (!hasShownReminder) {
      uni.showModal({
        title: 'Êï∞ÊçÆÂÆâÂÖ®ÊèêÈÜí',
        content: 'ËØ∑Ê≥®ÊÑèÔºöÂú®ÂæÆ‰ø°ËÆæÁΩÆ‰∏≠ÈÄâÊã©"Ê∏ÖÁêÜÂ∞èÁ®ãÂ∫èÁºìÂ≠ò"‰ºöÂØºËá¥ÊâÄÊúâÊï∞ÊçÆË¢´Âà†Èô§„ÄÇÂª∫ËÆÆÂÆöÊúü‰ΩøÁî®ÂØºÂá∫ÂäüËÉΩÂ§á‰ªΩÊÇ®ÁöÑÊï∞ÊçÆÔºå‰ª•Èò≤Êï∞ÊçÆ‰∏¢Â§±„ÄÇ',
        confirmText: 'ÊàëÁü•ÈÅì‰∫Ü',
        success: (res) => {
          if (res.confirm) {
            uni.setStorageSync('has_shown_data_loss_reminder', true)
          }
        }
      })
    }
  },

  onHide() {
    this.stopClock()
  },

  onUnload() {
    this.stopClock()
  }
})
</script>

<style>
.container {
  background-color: #fff;
  color: #2c3e50;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding: 20rpx 40rpx;
}

.container, .header {
  box-sizing: border-box;
}

.header {
  background: #fff;
  left: 0;
  padding: 20rpx 40rpx 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}

.date-info {
  align-items: center;
  display: flex;
  gap: 20rpx;
  justify-content: space-between;
}

.date-info .date-content {
  display: flex;
  flex-direction: column;
  gap: 10rpx;
}

.date-info .date-content .year {
  color: #2c3e50;
  font-size: 32rpx;
  font-weight: 400;
}

.date-info .date-content .month-week {
  color: #5c6b7a;
  font-size: 24rpx;
}

.date-info .flip-clock {
  align-items: center;
  background: #f5f7fa;
  border-radius: 16rpx;
  box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, .1);
  display: flex;
  height: 100%;
  padding: 12rpx 24rpx;
}

.date-info .flip-clock .time-unit {
  background: #edf0f5;
  border-radius: 8rpx;
  color: #2c3e50;
  font-size: 36rpx;
  font-weight: 500;
  min-width: 52rpx;
  padding: 8rpx 12rpx;
  position: relative;
  text-align: center;
  transition: all .3s ease;
}

.date-info .flip-clock .time-unit::before {
  background: rgba(0, 0, 0, .1);
  content: "";
  height: 2rpx;
  left: 0;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.date-info .flip-clock .time-separator {
  animation: blink 1s infinite;
  color: #ff9f0a;
  font-size: 36rpx;
  font-weight: 700;
  margin: 0 6rpx;
}

.header-row {
  align-items: center;
  justify-content: space-between;
  margin-top: 10rpx;
}

.habit-stats, .header-row {
  display: flex;
}

.habit-stats {
  gap: 40rpx;
}

.habit-stats .stat-item {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.habit-stats .stat-item .stat-value {
